import React, { useEffect, useState } from 'react';
import Buscador from './Buscador';

import "leaflet/dist/leaflet.css";
import L from 'leaflet';

const Map = () => {

    const [map2, setmap2]  = useState();
    const [meds,setmeds] = useState();
    const [medsBackup, setmedsBackup] = useState();
    const [Buscarf, setBuscarf] = useState('');
    
   
    var marker = new Array();
    var map;

    useEffect(()=>{
        fetch('https://prueba-prototipo2.firebaseio.com/.json')
             .then(data => data.json())
             .then(data => {
                 setmeds(data);
                 setmedsBackup(data); 
             })      
        mfarma();
    },[])

    // useEffect(()=>{
    //     filter();
    // },[Buscarf])

     const mfarma = () =>{
        const map = L.map('map').fitWorld();
        L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",{
            detectRetina:true,
            maxZoom:20,
            maxNativeZoom:20
        }).addTo(map);

        const fIcon = L.icon({
            iconUrl: require('../img/pimp.svg'),
            iconSize:[38, 95],
            iconAnchor:[22, 94],
            popupAnchor:[-3, -76]
        });

        map.locate({setView: true, maxZoom: 16, enableHighAccuracy: true});
        map.on('locationfound', e =>{
            const coords = [e.latlng.lat, e.latlng.lng];
            L.marker(coords, {icon: fIcon}).bindPopup('te encuentras aqui').addTo(map);
        })
        console.log(map);
        setmap2(map)
    } 

    const filter = (txto)=>{
    // const filter = ()=>{
        
        //obtener el texto del input del buscador

        if(marker!=null){
            for(let i=0;i<marker.length;i++) {
                map.removeLayer(marker[i]);
            }
            marker = [];
        }
      
        var text =  txto;
        
        //muestra el texot del input del buscador
        console.log(text);
        //obtener datos del array del stado
        const data = medsBackup;

        
        console.log(medsBackup);
        console.log(data);
  
        var newData = [];

        data.forEach((farma,index)=>{
            //console.log(farma);
            for(let far in farma.medicamentos){
                //console.log(far);
                //console.log(farma.medicamentos[far].existencia);
                //console.log(text);
                if(farma.medicamentos[far].nombre == text.toLowerCase() && farma.medicamentos[far].existencia == "si"){
                    newData.push(farma);
                    console.log(farma.medicamentos[far].nombre);
                }  
            }
        })

       
        console.log(newData);
        
        if(newData!=null){
            setmeds(newData);
            
        }   
        

        console.log(meds);
       // console.log(this.state.meds[0].nombre);
           

      }
 
    const markers = () =>{
        map = map2;
        const fIcon = L.icon({
            iconUrl: require('../img/pimp.svg'),
            iconSize: [38, 95],
            iconAnchor: [22, 94],
            popupAnchor: [-3, -76],
        });

        const farmacias = meds;
        

        
         var availableTags = JSON.stringify(farmacias);
         var farmacia = JSON.parse(availableTags.replace(/&quot;/g,'"').replace('amp;',''));

         for (var i=0; i< farmacia.length; i++){
            
            var mark = farmacia[i]; 
            console.log(farmacia[i]);
            var LamMarker = L.marker([mark.latitud, mark.longitud],{ icon: fIcon, title:` Farmacia ${mark.nombre}, Direccion:${mark.direccion}  `});
            marker.push(LamMarker);
            map.addLayer(marker[i]);
          }
        };

        const styles = {
           maxWidth: '100%'
        };

        return (
        <div className="row"> 
                
               <Buscador filtro={(txto) => filter(txto)}/>
              
               {/* <Buscador filtro={(txto) => setBuscarf(txto)}/> */}
                <div className="center-align">                
                    <button className="waves-effect waves-light btn-large  hoverable " onClick={markers}>Buscar</button>
                </div>
                <br></br>
                <div className="container mapa  z-depth-3" style={styles} id="map"></div>

                

        </div>
        );   
};

export default Map;