import React,{useEffect, useState} from 'react';
import "./css.css";
import styled from 'styled-components';
import { async } from 'q';
import {
   Magnifier,
   GlassMagnifier,
   SideBySideMagnifier,
   PictureInPictureMagnifier,
   MOUSE_ACTIVATION,
   TOUCH_ACTIVATION
 } from "react-image-magnifiers";


const Pmascarillas = () =>{
   
       useEffect(()=>{
              fetchItems();
              
          },[]);
      
         
      
        const[items, setItems] = useState([]);       
       
      
          const fetchItems = async () =>{
          const data = await fetch('https://publicidad-34d6e.firebaseio.com/.json');
          const items = await data.json();
          console.log(items);
          setItems(items);
          
          }   

    const Title = styled.h1`
       color:blue;
       font-size: 100px;
    `
    const PGrid = styled.div`
       display:grid;
       grid-template-columns: auto auto auto;
       @media (max-width: 750px) {
          grid-template-columns: auto;
      }
    `
    const Pitem = styled.div`
       pading: 5px 5px;
       margin: 5px;
      `

    const styleimg = {
       height:"220px",
    }

   

    return(
     <div>
        
         <PGrid>
         {items.map(item =>(
              <Pitem>               
                 <div class="card">
                     <div class="card-image waves-effect waves-block waves-light" >
                        {/* <img  class="activator responsive-img" style={styleimg} src={item.imgurl}></img> */}
                          <GlassMagnifier 
                              style={styleimg}
                              imageSrc={item.imgurl}
                              imageAlt="farmaco, laboratorio, medicina"
                              
                           />
                     </div>
                     <div class="card-content">
                        <span class="card-title activator grey-text text-darken-4">{item.nombre}<i class="small material-icons right  teal-text text-darken-4">info_outline</i></span>
                        <p><a href={`https://wa.me/505${item.numero}`}  target="_blank">Contacto</a></p>
                     </div>
                     <div class="card-reveal">
                        <span class="card-title grey-text text-darken-4">{item.nombre}<i class="material-icons right">close</i></span>
                        <p>{item.texto}</p>
                     </div>
                  </div>
              </Pitem>
          ))}
         </PGrid>
      </div>
    );


}

export default Pmascarillas;