import React,{useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import { async } from 'q';



const Noticias = () =>{
    

    useEffect(()=>{
        fetchItems();
    },[]);

   

   const[items, setItems] = useState([]);

    const fetchItems = async () =>{
    const data = await fetch('https://prototipo-noticias-a7a8c.firebaseio.com/.json');
    const items = await data.json();
    console.log(items);
    setItems(items);
    }
    
    
    const styles1 = {
        width:"10px"
    }
     

    
    return(
         <div className="center-align">
           
           <div>
            {/* {items.map(item =>(
            <h3 style={styles1} key={item.id}>
              <Link  to={`/Noticias/${item.id}/${item.titulo}/${item.descripcion}/${item.texto}` }  >{item.titulo}</Link>
            </h3>
            ))} */}


                
                {items.map(item =>(
                 <div  class="row " key={item.id}>
                        <div class="col s12 m7">
                        <div class="card">
                            <div class="card-image">
                            <img src={item.imgurl}></img>
                            <span class="card-title">{item.titulo}</span>
                            </div>
                            <div class="card-content">
                            <p>{item.descripcion}</p>
                            </div>
                            <div class="card-action">
                            
                            <Link  to={`/Noticias/${item.id}/${item.titulo}/${item.descripcion}/${item.texto}/${item.imgurl}/` }  >ver mas</Link>
                            </div>
                            </div>
                            </div>
                    </div>
                 ))}





            </div>

        </div>
       
 
    );


}

export default Noticias;